<template>
    <view class="demand_freight">
        <cu-custom bgColor="" :isBack="true"><block slot="backText"></block><block slot="content">我发布的货单</block></cu-custom>
        <view class="demand_freight_list">
            <!-- 导航栏 -->
            <scroll-view scroll-x class="bg-white nav">
                <view class="flex text-center navBox">
                    <view class="cu-item flex-sub" :class="item.id==tabCur?'things_active_color':''" v-for="(item,index) in tabData" :key="index" @tap="tabSelect" :data-id="item.id">
                        {{item.name}}
                    </view>
                </view>
            </scroll-view>
            <!-- 审核中-列表 -->
            <scroll-view v-if="tabCur == 1" class="freight_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="freight_item" v-for="(item,index) in listData[0].list" :key="index">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info" :data-id="item.orderId" @tap="linkToOrderFreightInfo">
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型: {{item.goodsType}}</view>
                            <view class="freight_right_info">运输总价格: ￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">运输单价：￥{{item.unitPrice}}</view>
                        <view class="freight_single_info">重量：{{item.weight}}吨</view>
                        <view class="freight_single_info">开始时间：{{item.startTime}}</view>
                        <view class="freight_single_info">结束时间：{{item.endTime}}</view>
                        <view class="freight_single_info">发布时间：{{item.createTime}}</view>
                        <view class="freight_single_info">订单编号：{{item.orderNo}}</view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" :data-index="index" :data-id="item.orderId" @tap.stop="cancelOrderFun">取消订单</button>
                    </view>
                </view>
                <view v-if="listData[0].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 未通过-列表 -->
            <scroll-view v-if="tabCur == 2" class="freight_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="freight_item" v-for="(item,index) in listData[1].list" :key="index">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info" :data-id="item.orderId" @tap="linkToOrderFreightInfo">
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型: {{item.goodsType}}</view>
                            <view class="freight_right_info">运输总价格: ￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">运输单价：￥{{item.unitPrice}}</view>
                        <view class="freight_single_info">重量：{{item.weight}}吨</view>
                        <view class="freight_single_info">开始时间：{{item.startTime}}</view>
                        <view class="freight_single_info">结束时间：{{item.endTime}}</view>
                        <view class="freight_single_info">订单编号：{{item.orderNo}}</view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-between align-center operating">
                        <view class="time">发布时间：{{item.createTime}}</view>
                        <button class="cu-btn bg-white grabOrderBtn" :disabled="buttonType" :data-id="item.orderId" @tap.stop="linkToUpdate">修改发布</button>
                    </view>
                    <!-- 拒绝原因 -->
                    <view class="reason">原因：{{item.release_examine_remark?item.release_examine_remark:'暂无'}}</view>
                </view>
                <view v-if="listData[1].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 待确认-列表 -->
            <scroll-view v-if="tabCur == 3" class="freight_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="freight_item" v-for="(item,index) in listData[2].list" :key="index">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info" :data-id="item.orderId" @tap="linkToOrderFreightInfo">
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型: {{item.goodsType}}</view>
                            <view class="freight_right_info">运输总价格: ￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">运输单价：￥{{item.unitPrice}}</view>
                        <view class="freight_single_info">重量：{{item.weight}}吨</view>
                        <view class="freight_single_info">开始时间：{{item.startTime}}</view>
                        <view class="freight_single_info">结束时间：{{item.endTime}}</view>
                        <view class="freight_single_info">订单编号：{{item.orderNo}}</view>
                        <view class="freight_single_info order_type">订单状态：<text class="type_text">运输中</text></view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" :data-index="index" :data-id="item.orderId" @tap.stop="cancelOrderFun">取消订单</button>
                        <button class="cu-btn bg-white detail_btn" :disabled="buttonType" :data-id="item.orderId" @tap.stop="linkToOrderFreightInfo">订单详情</button>
                    </view>
                </view>
                <view v-if="listData[2].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 交易中-列表 -->
            <scroll-view v-if="tabCur == 4" class="freight_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="freight_item" v-for="(item,index) in listData[3].list" :key="index">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info" :data-id="item.orderId" @tap="linkToOrderFreightInfo">
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型: {{item.goodsType}}</view>
                            <view class="freight_right_info">运输总价格: ￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">运输单价：￥{{item.unitPrice}}</view>
                        <view class="freight_single_info">重量：{{item.weight}}吨</view>
                        <view class="freight_single_info">开始时间：{{item.startTime}}</view>
                        <view class="freight_single_info">结束时间：{{item.endTime}}</view>
                        <view class="freight_single_info">订单编号：{{item.orderNo}}</view>
                        <view class="freight_single_info order_type">订单状态：<text class="type_text">运输中</text></view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" :data-index="index" :data-id="item.orderId" @tap.stop="cancelOrderFun" v-if="item.status <= 4">取消订单</button>
                        <button class="cu-btn bg-white confirm_transit_btn" :disabled="buttonType" :data-id="item.orderId" @tap.stop="linkToPay" v-if="item.status === 4">支付货款</button>
                        <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" :data-index="index" :data-id="item.orderId" @tap.stop="getCallFun" v-if="item.status === 9">拒绝收货</button>
                        <button class="cu-btn bg-white complete_transit_btn" :disabled="buttonType" :data-id="item.orderId" @tap.stop="confirmReceiptFun" v-if="item.status === 9">确认收货</button>
                    </view>
                </view>
                <view v-if="listData[3].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 已完成-列表 -->
            <scroll-view v-if="tabCur == 5" class="freight_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="freight_item" v-for="(item,index) in listData[4].list" :key="index">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info" :data-id="item.orderId" @tap="linkToOrderFreightInfo">
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型: {{item.goodsType}}</view>
                            <view class="freight_right_info">运输总价格: ￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">运输单价：￥{{item.unitPrice}}</view>
                        <view class="freight_single_info">重量：{{item.weight}}吨</view>
                        <view class="freight_single_info">开始时间：{{item.startTime}}</view>
                        <view class="freight_single_info">结束时间：{{item.endTime}}</view>
                        <view class="freight_single_info">订单编号：{{item.orderNo}}</view>
                        <view class="freight_single_info order_type">订单状态：<text class="type_text">已完成</text></view>
                    </view>
                    <!-- 操作区域 -->
                    <!-- <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white detail_btn" :data-id="item.orderId" @tap.stop="linkToEvaluate">评价</button>
                    </view> -->
                </view>
                <view v-if="listData[4].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { userReleaseGoodsList_api,demandCancelOrders_api,confirmReceiptGrabsGoods_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            // 全局按钮状态
            buttonType: false,
            appBaseData:null,//全局配置信息
            userInfo: {},
            tabCur: 1,
            scrollLeft: 0,
            tabData: [
                {
                    id: 1,
                    value: 10,
                    name: '审核中',
                },
                {
                    id: 2,
                    value: 20,
                    name: '未通过'
                },
                {
                    id: 3,
                    value: 30,
                    name: '待确认'
                },
                {
                    id: 4,
                    value: 40,
                    name: '交易中'
                },
                {
                    id: 5,
                    value: 50,
                    name: '已完成'
                }
            ],
            listData: [
                // 审核中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 未通过
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 待确认
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 运输中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 已完成
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                }
            ],
            // 全局按钮状态
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.base_refreshPage': function(val) {
            if (val) {
                // 更新数据
                that.clearPageFun();
                that.base({name:'refreshPage',value:false});
            }
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    computed: {
        ...mapGetters(['base_cancelOrderType']) 
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.listData = [
                // 审核中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 未通过
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 待确认
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 运输中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 已完成
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                }
            ];
            that.getFreightOrder();
        },
        /**导航选择
         * @description: 
         * @param {*} e
         * @return {*}
         */
        tabSelect(e) {
            if(that.tabCur == e.currentTarget.dataset.id) return false;
            that.tabCur = e.currentTarget.dataset.id;
            that.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
            // 判断当前列表有没有数据,没有就qui请求数据
            if (that.listData[that.tabCur - 1].list.length <= 0 && that.listData[that.tabCur - 1].pageMore) {
                that.getFreightOrder();
            }
        },
        /**拨打电话
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getCallFun(e) {
            uni.makePhoneCall({
                phoneNumber: '15626236839'
            });
        },
        /**确认收货
         * @description: 
         * @param {*} e
         * @return {*}
         */
        confirmReceiptFun(e) {
            that.buttonType = true;
            let params = {
                release_order_id: e.currentTarget.dataset.id
            };
            confirmReceiptGrabsGoods_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'完成收货'}});
                that.listData[3] = {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                };
                that.getFreightOrder();
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**取消订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        cancelOrderFun(e) {
            that.buttonType = true;
            let params = {
                order_id: e.currentTarget.dataset.id,
                type: that.base_cancelOrderType[0].value,
            };
            demandCancelOrders_api(params,that).then(res=>{
                that.$delete(that.listData[that.tabCur-1].list, e.currentTarget.dataset.index);
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'取消成功'}});
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**跳转更新订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToUpdate(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/freight/updateOrder/index?goods_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**跳转到支付
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToPay(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/freight/pay/index?order_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**跳转到评价
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToEvaluate(e) {
            uni.navigateTo({
                url: `/pages/things/user/demand/freight/assess/index?id=${e.currentTarget.dataset.id}`
            });
        },
        /** 跳转到车单详情
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToOrderFreightInfo(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/freight/info/index?order_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**获取发布的车单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFreightOrder(e) {
            let params = {
                page: that.listData[that.tabCur-1].page,
                page_size: that.listData[that.tabCur-1].page_size,
                status: that.tabData[that.tabCur-1].value
            };
            userReleaseGoodsList_api(params,that).then(res=>{
                that.updateListData(res.list,res.total);
            }).catch(err=>{
                console.log(err);
            })
        },
        /**更新数据
         * @description: 
         * @param {*} data 数组列表
         * @param {*} total 列表总长度
         * @return {*}
         */
        updateListData(data=[],total=0) {
            switch (Number(that.tabData[that.tabCur-1].value)) {
                case 10:
                    that.listData[0].list = that.listData[0].list.concat(data);
                    if (that.listData[0].list.length >= total) {
                        that.listData[0].pageMore = false;
                    }else{
                        that.listData[0].page++;
                    }
                    break;
                case 20:
                    that.listData[1].list = that.listData[1].list.concat(data);
                    if (that.listData[1].list.length >= total) {
                        that.listData[1].pageMore = false;
                    }else{
                        that.listData[1].page++;
                    }
                    break;
                case 30:
                    that.listData[2].list = that.listData[2].list.concat(data);
                    if (that.listData[2].list.length >= total) {
                        that.listData[2].pageMore = false;
                    }else{
                        that.listData[2].page++;
                    }
                    break;
                case 40:
                    that.listData[3].list = that.listData[3].list.concat(data);
                    if (that.listData[3].list.length >= total) {
                        that.listData[3].pageMore = false;
                    }else{
                        that.listData[3].page++;
                    }
                    break;
                case 50:
                    that.listData[4].list = that.listData[4].list.concat(data);
                    if (that.listData[4].list.length >= total) {
                        that.listData[4].pageMore = false;
                    }else{
                        that.listData[4].page++;
                    }
                    break;
                default:
                    break;
            }
        },
        /**上拉加载
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getMoreData(e) {
            let getType = false;
            switch (Number(that.tabData.value)) {
                case 10:
                    getType = that.listData[0].pageMore;
                    break;
                case 20:
                    getType = that.listData[1].pageMore;
                    break;
                case 30:
                    getType = that.listData[2].pageMore;
                    break;
                case 40:
                    getType = that.listData[3].pageMore;
                    break;
                case 50:
                    getType = that.listData[4].pageMore;
                    break;
                default:
                    break;
            }
            if (!getType) return false;
            that.getFreightOrder();
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        that.tabCur = options.type?options.type:1;
        that.getFreightOrder();
    }
}
</script>

